<template>
	<div class="medium-6 cell">
		<label v-bind:class="{'color_alert': pageHasError()}">Password</label>
		<div class="input-group m-b_4">
			<span class="input-group-label p-l_3 p-r_3">
				<i class="fal fa-key"></i>
			</span>
			<input
				v-if="!passwordShow"
				class="input-group-field"
				type="password"
				v-model="password"
				placeholder="password"
				required
				v-bind:class="{' br_alert': pageHasError()}"
			>
			<input
				v-if="passwordShow"
				class="input-group-field"
				type="text"
				v-model="password"
				placeholder="password"
				required
				v-bind:class="{' br_alert': pageHasError()}"
			>
			<div class="input-group-button" v-if="password !=''">
				<button
					type="button"
					class="button secondaryalt hollow p-l_3 p-r_3"
					@click="password=''"
				>
					<i class="fas fa-times"></i>
				</button>
			</div>
			<div class="input-group-button">
				<button
					type="button"
					class="button secondaryalt hollow p-l_3 p-r_3"
					@click="passwordShowToggle"
				>
					<i v-if="passwordShow" class="fas fa-eye"></i>
					<i v-if="!passwordShow" class="fas fa-eye-slash"></i>
				</button>
			</div>
		</div>
	</div>
</template>

<script>
    export default {
        name: "inputEmail",
        props: {
            label:{type:String, default:"Password"},

        },
        data () {
            return {
                password:""
            }
        }
    }
</script>

